// Handles styling for the coding examples page (examples.html)

.coding {

    // Section container

    &__section {
        text-align: center;
        background-image: url(../img/earth-center.jpg);
        @include background-image-styles();
        min-height: 100vh;
    }

    // Horizontal page header

    &__header {
        @include sibling-page-header-styles();
        background-image: url(../img/helix.jpg);
        @include background-image-styles();
        @include hamburger-icon-styles(25px);
        @include media-query (small) {
            justify-content: center;
        }
    }

    // Page header title

    &__title {
        color: $aqua;
        @include section-title-styles();
    }

    // Page items (divs)

    &__item {
        @include page-item-div-styles();
        margin-bottom: em(30px);

        &-title {
            font-size: em(30px);
        }
    }

    &__text {
        display: flex;
        flex-direction: column;
        justify-content: center;
        row-gap: em(30px);
        text-align: left;
    }
    
    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        @include media-query (x-large) {
            flex-direction: row;
        }
    }

    &__snippet {
        margin-bottom: em(30px);
        border: 3px solid $aqua;
        border-radius: em(5px);

        @include media-query (x-large) {
            margin-right: em(30px);
        }
    }
}

.snip-1 {
    width: 100%;
    @include media-query (x-large) {
        width: em(550px);
    }
    
}

.snip-2 {
    width: 100%;
    @include media-query (x-large) {
        width: em(550px);
        height: em(150px);
    }
}
